<template>
  <component v-bind:is="layout"/>
</template>

<script>
import Default from "@/layouts/Default.vue";
import Admin from "@/layouts/Admin.vue";

export default {
  name: 'App',
  components: {
    Default,
    Admin
  },
  computed: {
    layout(){
      console.log(this.$route)
      return this.$route.meta.layout || 'default'
    }
  }
};
</script>