import API from './product.api.js';

//Khởi tạo state mặc định
const initialState = () => ({
  items: [],
  //giá trị lưu tạm thời dùng cập nhật dữ liệu mới hoặc chỉnh sửa 
  itemTemp: {}
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();


/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  itemTemp: state => state.itemTemp,
  items: state => state.items,
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  //cập nhật lại số lượng ban đầu của sp
  INTIAL_QUANTITY (state, data) {
    let product = state.items.find((record) => record.id === data.id);
    product.quantity = data.stock;
  },
  //tăng 1 sp của 1 mặt hàng
  INCREMENT_QUANTITY (state, data) {
    let product = state.items.find((record) => record.id === data.id);
    product.quantity++;
  },
  //giảm 1 sp của 1 mặt hàng
  DECREMENT_QUANTITY (state, data) {
    let product = state.items.find((record) => record.id === data.id);
    product.quantity--;
  },
  SETUP (state, data) {
    state.items = data
  },
  SET (state, data) {
    state.itemTemp = data
  },
  SET_BY_ID (state, dataId) {
    if(!dataId) return;
    let product = state.items.find((record) => record.id === dataId);
    if(product){
      state.itemTemp = product
    }
  },
  ADD (state) {
    state.items.push(state.itemTemp)
  },
  EDIT (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
    state.items = items
    state.itemTemp = data
  },
  REMOVE (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
  },
  CLEAN (state) {
    state.itemTemp = {}
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setup: async ({commit}) => {
    let data = await API.GET_ALL();
    commit('SETUP', data)
  },
  set: async ({commit}, data) => {
    commit('SET', data)
  },
  add: async ({commit}) => {
    commit('ADD')
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    commit('REMOVE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}