//Khởi tạo state mặc định
const initialState = () => ({
  items: [],
  //giá trị lưu tạm thời dùng cập nhật dữ liệu mới hoặc chỉnh sửa 
  itemTemp: {}
});

/**
 * State object
 * Use: this.$store.state.Namemodule.key
 */
const state = initialState();

/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  itemTemp: state => state.itemTemp,
  items: state => state.items,
  size: state =>  state.items.length,
  totalAmount: (state) => {
    // reduce : total giá trị ban đầu = 0 , lặp cộng dồn cho tới cuối mảng
    return state.items.reduce((total, record) => {
      return total + (record.price * record.quantity);
    }, 0);
  },
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SETUP (state, data) {
    state.items = data
  },
  SET (state, data) {
    state.itemTemp = data
  },
  ADD (state, data) {
    // kiểm tra cart
    let cart = state.items.find((record) => record.id === data.id);

    if (cart) {
      // sp có sẳn thì tăng số lượng
      cart.quantity++;
    } else {
      // sp mới thì cho vào giỏ
      state.items.push({
        ...data,
        stock: data.quantity, // số lượng gốc còn trong kho
        quantity: 1,
      });
    }
  },
  EDIT (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
    state.items = items
    state.itemTemp = data
  },
  REMOVE (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
  },
  DELETE (state, data) {
    let cart = state.items.find((record) => record.id === data.id);
    // giảm số lượng
    cart.quantity++;
  },
  CLEAN (state) {
    state.itemTemp = {}
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  setup: async ({commit}) => {
    let data = await API.GET_ALL();
    commit('SETUP', data)
  },
  set: async ({commit}, data) => {
    commit('SET', data)
  },
  add: async ({commit}, data) => {
    
    // Tăng 1 sp trong giỏ
    commit('ADD', data)

    // Giảm 1 sp trong kho
    commit('Product/DECREMENT_QUANTITY', data, { root: true })
    
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    
    // xoá hết sp của 1 mặt hàng trong giỏ
    commit('REMOVE', data)
    
    // Cập nhật lại số lượng ban đầu của sp
    commit('Product/INTIAL_QUANTITY', data, { root: true })
  },
  delete: async ({commit}, data) => {
    
    // Giảm 1 sp trong giỏ
    commit('DELETE', data)
    
    // Tăng 1 sp trong kho
    commit('Product/INCREMENT_QUANTITY', data, { root: true })
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}