<template>
  <div class="menu-page page">
    <div class="container">

      <div class="nut-menu">
        <span class="arrow-wrap">
          <i class="arrow right"></i>
        </span>
        <h2 class="popup design" @click="showMenu">
          <span>Menu</span>
        </h2>
        <span class="arrow-wrap ar">
          <i class="arrow left"></i>
        </span>
      </div>

      <van-popup
        v-model="popupMenu"
        closeable
        position="right"
        :style="{ 'height': '100%', 'min-width': '240px' }"
      >
        <div class="popup-menu" v-if="productsByType && productsByType.length">
          <div class="popup-title">Extend Menu</div>
          <van-checkbox-group v-model="checkboxMenu" ref="cboxs">
            <van-cell-group>
              <van-cell
                clickable
                title="Select all"
                ref="cboxAll"
                @click="toggleAll()"
              >
                <template #right-icon>
                  <van-checkbox name="all" />
                </template>
              </van-cell>
              <van-cell
                v-for="(pt, k) in productsByType"
                clickable
                :key="k"
                :title="pt.type"
                @click="toggle(k)"
              >
                <template #right-icon>
                  <van-checkbox :name="pt.type" ref="cbox" />
                </template>
              </van-cell>
            </van-cell-group>
          </van-checkbox-group>
        </div>
      </van-popup>

      <div class="row around-xs" v-if="productsByType && productsByType.length">
        <template v-for="(pt, k) in productsByType">
          <template v-for="(c, ck) in checkboxMenu">
          <div class="col-xs-12 col-sm-6 col-md-4" v-if="pt.type === c" :title="pt.typeT" :key="k+ck">
            <div class="vbox-wrap">
              <h3 class="design">
                <span>{{pt.type}}</span>
              </h3>
              <product :item="p" v-for="(p, i) in pt.data" :key="i"/>
            </div>
          </div>
          </template>
        </template>
      </div>

    </div>

    <loader v-if="loading" />
  </div>
</template>
<script>
import _ from 'lodash';
import Product from "@/components/Product.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: 'menu-page',
  components: {
    Product,
    Loader
  },
  data() {
    return {
      loading: true,
      checkboxToggle: true,
      checkboxMenu: ['all'],
      popupMenu: false,
      list: [
        {"key": "Cafe 1", "value": true},
        {"key": "Cafe 2", "value": true},
      ],
      agileOpts:{
        slidesToShow: 2,
        navButtons: false,
        autoplay: true,
        autoplaySpeed: 5000,
        responsive: [
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 4,
            },
          },
          {
            breakpoint: 970,
            settings: {
              slidesToShow: 6,
            },
          },
        ]
      },
      images: [
        "/img/800x450/1.jpg",
        "/img/800x450/2.jpg",
        "/img/800x450/3.jpg",
        "/img/800x450/4.jpg",
        "/img/800x450/5.jpg",
        "/img/800x450/6.jpg",
      ],
    };
  },
  // watch: {
  //   checkboxMenu: function (current, old) {
  //     console.log(current)
  //   }
  // },
  methods: {
    showMenu() {
      this.popupMenu = true;
    },
    onConfirm() {
      this.$refs.list.toggle();
    },
    toggle(index) {
      this.$refs.cbox[index].toggle();
    },
    toggleAll() {
      let vm = this;
      vm.checkboxToggle = !vm.checkboxToggle;
      vm.$refs.cboxs.toggleAll(vm.checkboxToggle);
    }
  },
  computed: {
    products: function (){
      return this.$store.getters['Product/items']
    },
    productsByType: function(){
      var vm = this;
      var grouped = _.chain(vm.products)
      .groupBy("type")
      .map(function(value, key) {
        // var type = key.replace('Cafe/','');
        return {
          data: value,
          type: key,
          typeT: _.truncate(key, {length: 10})
        }
      }).value()
      return grouped;
    }
  },
  async created(){
    let vm = this;
    await vm.$store.dispatch('Product/setup');

    vm.loading = false;

    let type = vm.$route.query.type;
    for (const pt in vm.productsByType) {
      if(type){
        if(type === removeUnicodeSpace(vm.productsByType[pt].type)){
          vm.checkboxMenu = [vm.productsByType[pt].type];
          vm.checkboxToggle = false;
          return;
        }
      }
      else{
        vm.checkboxMenu.push(vm.productsByType[pt].type)
      }
    }
  },
  // updated(){
  //   console.log(1)
  //   this.toggleAll()
  // }
  
};
</script>
