<template>
  <div class="home-page page">
    <div class="container">
      <agile :options="agileOpts" v-if="products && products.length">
        <template v-for="(product, index) in products">
          <div class="slide" v-if="index < 6" :key="index">
            <img v-lazy="product.pics[0]" />
            <div class="design">{{product.title}}</div>
          </div>
        </template>
      </agile>

      <van-tabs v-model="tabs" scrollspy sticky v-if="productsByType && productsByType.length">
        <van-tab class="col-xs-12 col-sm-6 col-md-4" v-for="(pt, k) in productsByType" :title="pt.typeT" :key="k">
          <div class="vbox-wrap">
            <h3 class="link design" @click="redirectMenu(pt.type)">
              <span>{{pt.type}}</span>
            </h3>
            <product :item="p" @clicked="onClicked" v-for="(p, i) in pt.data" :key="i"/>
          </div>
         
        </van-tab>
      </van-tabs>
    </div>

    <van-popup v-model="popup" class="vpopup" closeable>
      <template v-if="product">
        <product :item="product" class="vertical"/>
      </template>
    </van-popup>

    <loader v-if="loading" />
  </div>
</template>
<script>
import _ from 'lodash';
import Product from "@/components/Product.vue";
import Loader from "@/components/Loader.vue";

export default {
  name: 'home-page',
  components: {
    Product,
    Loader
  },
  data() {
    return {
      loading: true,
      tabs: 0,
      popup: false,
      product: null,
      agileOpts:{
        slidesToShow: 2,
        navButtons: false,
        autoplay: true,
        autoplaySpeed: 5000,
        responsive: [
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 4,
            },
          },
          {
            breakpoint: 970,
            settings: {
              slidesToShow: 6,
            },
          },
        ]
      },
      images: [
        "/img/800x450/1.jpg",
        "/img/800x450/2.jpg",
        "/img/800x450/3.jpg",
        "/img/800x450/4.jpg",
        "/img/800x450/5.jpg",
        "/img/800x450/6.jpg",
      ],
    };
  },
  computed: {
    products: function (){
      return this.$store.getters['Product/items']
    },
    productsByType: function(){
      var vm = this;
      var grouped = _.chain(vm.products)
      .groupBy("type")
      .map(function(value, key) {
        // var type = key.replace('Cafe/','');
        return {
          data: value,
          type: key,
          typeT: _.truncate(key, {length: 10})
        }
      }).value()
      return grouped;
    }
  },
  methods: {
    onClicked(data){
      console.log(data)
      let vm = this;
      vm.product = data;
      vm.popup = true;
    },
    redirectMenu(type){
      return this.$router.push('/menu?type='+removeUnicodeSpace(type))
    }
  },
  async created(){
    let vm = this;
    await vm.$store.dispatch('Product/setup');
    vm.loading = false;
  }
  
};
</script>
