import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Menu from '@/views/Menu.vue'
import Cart from '@/views/Cart.vue'
import Placement from '@/views/Placement.vue'

import Dashboard from '@/admin/Dashboard.vue'
import Revenue from '@/admin/Revenue.vue'
import Bill from '@/admin/Bill.vue'
import Users from '@/admin/Users.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/menu',
    name: 'Menu',
    component: Menu
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart
  },
  {
    path: '/placement',
    name: 'Placement',
    component: Placement
  },
  {
    meta: {layout: 'Admin'},
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard,
    children: [
      {
        meta: {layout: 'Admin'},
        path: 'revenue',
        name: 'Revenue',
        component: Revenue
      },
      {
        meta: {layout: 'Admin'},
        path: 'bill',
        name: 'Bill',
        component: Bill
      },
      {
        meta: {layout: 'Admin'},
        path: 'users',
        name: 'Users',
        component: Users
      },
    ]
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
