//Khởi tạo state mặc định
const initialState = () => ({
  items: [],
  //giá trị lưu tạm thời dùng cập nhật dữ liệu mới hoặc chỉnh sửa 
  itemTemp: {
    'user': 'dung',
    'content': '',
    'time': null
  }
});

/**
 * State object
 * Use: this.$store.Namemodule.key
 */
const state = initialState();


/**
 * Getter functions
 * Use: this.$store.getters['Namemodule/key']
 */
const getters = {
  itemTemp: state => state.itemTemp,
  items: state => state.items.filter((data) => {return !data.completed}),
}

/**
 * Mutations
 * this.$store.commit('Namemodule/key', data)
 */
const mutations = {
  SET (state, data) {
    state.newItem = data
  },
  ADD (state) {
    state.itemTemp.time = new Date();
    state.items.push(state.itemTemp)
  },
  EDIT (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
    state.items = items
    state.itemTemp = data
  },
  REMOVE (state, data) {
    let items = state.items
    items.splice(items.indexOf(data), 1)
  },
  CLEAN (state) {
    state.itemTemp = {
      'user': '',
      'content': '',
      'time': null
    }
  },

  //cài đặt lại state
  RESET (state) {
    const newState = initialState();
    Object.keys(newState).forEach(key => {
      state[key] = newState[key]
    });
  },
}

/**
 * Actions
 * Use: this.$store.dispatch('Namemodule/key', data)
 */
const actions = {
  set: async ({commit}, data) => {
    commit('SET', data)
  },
  add: async ({commit}) => {
    commit('ADD')
  },
  edit: async ({commit}, data) => {
    commit('EDIT', data)
  },
  remove: async ({commit}, data) => {
    commit('REMOVE', data)
  },
  clean: async ({commit}) => {
    commit('CLEAN')
  },
  reset: async ({ commit }) =>  {
    commit('RESET');
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}