export default {
  GET_ALL: () => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve(products);
      }, 500);
    });
  },
  GET: (id) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        var item = products.filter((data) => {return data.id === id})
        resolve(item);
      }, 100);
    });
  },
  ADD: (item) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve(item);
      }, 100);
    });
  },
  EDIT: (item) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve(item);
      }, 100);
    });
  },
  REMOVE: (item) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve(item);
      }, 100);
    });
  }
}