<template>
  <div class="dashboard-page page">
    <div class="container">

      

    </div>

    <loader v-if="loading" />
  </div>
</template>
<script>
import _ from 'lodash';
import Loader from "@/components/Loader.vue";

export default {
  name: 'dashboard-page',
  components: {
    Loader
  },
  data() {
    return {
      loading: true,
    };
  },
  methods: {
  },
  computed: {
    products: function (){
      return this.$store.getters['Product/items']
    },
    productsByType: function(){
      var vm = this;
      var grouped = _.chain(vm.products)
      .groupBy("type")
      .map(function(value, key) {
        // var type = key.replace('Cafe/','');
        return {
          data: value,
          type: key,
          typeT: _.truncate(key, {length: 10})
        }
      }).value()
      return grouped;
    }
  },
  async created(){
    let vm = this;
    // await vm.$store.dispatch('Product/setup');

    vm.loading = false;
  },
  
};
</script>
