<template>
  <div class="vbox">
    <div class="vbox-head">
      <div class="vbox-pics">
        <img class="vbox-img" :src="item.pics[0]" @click="next(item)" />
      </div>
      <div class="vbox-tag" v-if="item.tag">{{item.tag}}</div>
    </div>
    <div class="vbox-body">
      <h3 class="vbox-title" @click="next(item)">
        {{item.title}}
      </h3>
      <template v-if="!cart">
      <div class="vbox-description" v-if="item.description">
        {{item.description}}
      </div>
      </template>
      <div class="vbox-money">
        <span class="vbox-currency">$</span>
        <span class="vbox-price">{{item.price}}</span>
        <span class="vbox-sale">{{(item.price*item.sale)+item.price}}</span>
        <span class="vbox-tick" v-if="cart">type</span>
      </div>
      <van-stepper v-model="item.quantity" :max="item.stock" v-if="cart"/>
      <van-rate
        v-if="rate"
        v-model="item.rating"
        allow-half
        void-icon="star"
        void-color="#eee"
      />
      <div class="vbox-more" v-if="more">
        <div class="vbox-more-title">Sugar</div>
        <van-radio-group v-model="sugar">
          <van-radio name="medium">medium</van-radio>
          <van-radio name="sweet">sweet</van-radio>
          <van-radio name="less">less</van-radio>
        </van-radio-group>
      </div>
    </div>
    <div class="vbox-footer" v-if="cart">
      <button class="vbox-remove" @click="remove(item)">Remove</button>
    </div>
    <div class="vbox-footer" v-else>
      <button class="vbox-like">Like</button>
      <button class="vbox-cart" @click="add(item)">Cart</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Product',
  props: ["item","cart","rate","more"],
  data() {
    return {
      sugar: null,
      rating: 0,
      quantity: 0,
      step: 0,
    };
  },
  methods: {
    add(data) {
      this.$store.dispatch("Cart/add", data);
    },
    remove(data) {
      this.$store.dispatch("Cart/remove", data);
    },
    next(data){
      console.log(data)
      this.$emit('clicked' , data);
    }
  },
  
};
</script>
